const ADD_MESSAGE = 'ADD-MESSAGE';
const UPDATE_NEW_MESSAGE_TEXT = 'UPDATE-NEW-MESSAGE-TEXT';

const initialState = {
  usersData: [{ id: '0', name: '  ' }],

  messagesData: [
    {
      id: '0',
      message: '. ,    .',
      isMyMessage: false,
    },
    { id: '1', message: ' !   ?', isMyMessage: true },
    {
      id: '2',
      message: '.      .   .',
      isMyMessage: false,
    },
    {
      id: '3',
      message:
        ' , .  ,     ,   .   .',
      isMyMessage: false,
    },
    {
      id: '4',
      message:
        '.   -  ! ,    !',
      isMyMessage: true,
    },
    { id: '5', message: ' ', isMyMessage: false },
    {
      id: '6',
      message:
        '     , .    - .',
      isMyMessage: true,
    },
    { id: '7', message: '    .', isMyMessage: false },
    { id: '8', message: '      ?', isMyMessage: true },
    {
      id: '9',
      message: '  .  . .',
      isMyMessage: false,
    },
    { id: '10', message: ' .', isMyMessage: false },
    { id: '11', message: '   .', isMyMessage: true },
    { id: '12', message: '    .', isMyMessage: false },
  ],

  newMessageText: '',
};

const messengerReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE: {
      return {
        ...state,
        messagesData: [
          ...state.messagesData,
          { id: '14', message: state.newMessageText, isMyMessage: true },
        ],
        newMessageText: '',
      };
    }

    case UPDATE_NEW_MESSAGE_TEXT: {
      return {
        ...state,
        newMessageText: action.newText,
      };
    }

    default:
      return state;
  }
};

export const addMessage = () => ({ type: ADD_MESSAGE });

export const updateNewMessageText = (text) => ({
  type: UPDATE_NEW_MESSAGE_TEXT,
  newText: text,
});

export default messengerReducer;
